import React, { useEffect, useState, useRef } from "react";
import { useNavigate, useLocation } from "react-router-dom";
import axios from "axios";

const SERVER_URL =
  "https://port-0-homerun-server-m3me4q5sa42dec9d.sel4.cloudtype.app";

const KakaoRedirect = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const [error, setError] = useState("");
  const calledRef = useRef(false);

  useEffect(() => {
    if (calledRef.current) return;
    calledRef.current = true;

    const processKakaoLogin = async () => {
      const params = new URLSearchParams(location.search);
      const code = params.get("code");

      if (!code) {
        setError("    .");
        setTimeout(() => navigate("/login", { replace: true }), 3000);
        return;
      }

      try {
        const response = await axios.get(`${SERVER_URL}/oauth`, {
          params: { code },
          headers: {
            "Cache-Control": "no-cache",
            Pragma: "no-cache",
            Accept: "application/json",
          },
          withCredentials: true,
        });

        console.log("Server response:", response.data);

        if (!response.data?.userData || !response.data?.tokenInfo) {
          throw new Error("Invalid response data");
        }

        const { userData, tokenInfo } = response.data;

        //   
        localStorage.clear(); //    

        const userInfo = {
          id: userData.id,
          nickname: userData.properties?.nickname || "",
          isKakao: true,
        };

        localStorage.setItem("kakaoUser", JSON.stringify(userInfo));
        localStorage.setItem(
          "kakaoToken",
          JSON.stringify({
            accessToken: tokenInfo.access_token,
            expiresIn: tokenInfo.expires_in,
            tokenType: tokenInfo.token_type,
          })
        );
        localStorage.setItem("isLoggedIn", "true");
        localStorage.setItem("loginType", "kakao");

        //   
        navigate("/", {
          replace: true,
          state: {
            loginSuccess: true,
            user: userInfo,
          },
        });
      } catch (error) {
        console.error("    :", error);
        console.error("  :", error.response?.data);

        setError(
          error.response?.data?.error ||
            error.response?.data?.message ||
            "    ."
        );

        setTimeout(() => navigate("/login", { replace: true }), 3000);
      }
    };

    processKakaoLogin();
  }, []); //           

  return (
    <div className="flex min-h-screen items-center justify-center bg-gray-100">
      <div className="rounded-lg bg-white p-8 shadow-md">
        <div className="text-center">
          {error ? (
            <>
              <h2 className="mb-2 text-xl font-semibold text-red-600">
                 
              </h2>
              <p className="text-red-500">{error}</p>
            </>
          ) : (
            <>
              <h2 className="mb-2 text-xl font-semibold">
                   ...
              </h2>
              <p className="text-gray-600"> .</p>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default KakaoRedirect;
